.note-sidebar {
  position: relative;
  width: 290px;
  border-right: 1px solid #ccc;
  background-color: #eee;
}
.note-sidebar .add-note {
  position: absolute;
  right: 5px;
  top: 12px;
  color: #666;
  font-size: 12px;
  padding: 2px 4px;
  box-shadow: 0px 0px 2px 0px #ccc;
  border: none;
  z-index: 1;
}
.note-sidebar .notebook-title {
  font-size: 18px;
  font-weight: normal;
  color: #333;
  height: 45px;
  line-height: 45px;
  text-align: center;
  border-bottom: 1px solid #ccc;
  background-color: #f7f7f7;
  display: block;
}
.note-sidebar .el-dropdown-link {
  cursor: pointer;
}
.note-sidebar .el-dropdown-menu__item {
  width: 200px;
}
.note-sidebar .menu {
  display: flex;
}
.note-sidebar .menu div {
  font-size: 12px;
  padding: 2px 10px;
  flex: 1;
  border-right: 1px solid #ccc;
  border-bottom: 1px solid #ccc;
}
.note-sidebar .menu div:last-child {
  border-right: none;
}
.note-sidebar .menu .iconfont {
  font-size: 10px;
}
.note-sidebar .notes li:nth-child(odd) {
  background-color: #f2f2f2;
}
.note-sidebar .notes li a {
  display: flex;
  padding: 3px 0;
  font-size: 12px;
  border: 2px solid transparent;
}
.note-sidebar .notes li .router-link-exact-active {
  border: 2px solid #81c0f3;
  border-radius: 3px;
}
.note-sidebar .notes li span {
  padding: 0 10px;
  flex: 1;
}
.note-detail {
  flex: 1;
  display: flex;
  flex-direction: column;
}
.note-detail .note-detail-ct {
  height: 100%;
}
.note-detail .note-empty {
  font-size: 50px;
  color: #ccc;
  text-align: center;
  margin-top: 100px;
}
.note-detail .note-bar {
  padding: 4px 20px;
  border-bottom: 1px solid #eee;
}
.note-detail .note-bar:after {
  content: '';
  display: block;
  clear: both;
}
.note-detail .note-bar span {
  font-size: 12px;
  color: #999;
  margin-right: 4px;
}
.note-detail .note-bar .iconfont {
  float: right;
  margin-left: 4px;
  font-size: 18px;
  cursor: pointer;
}
.note-detail .note-title input,
.note-detail .note-title span {
  display: inline-block;
  width: 100%;
  border: none;
  outline: none;
  font-size: 18px;
  padding: 10px 20px;
}
.note-detail .editor {
  height: calc(100% - 70px);
  position: relative;
}
.note-detail textarea,
.note-detail .preview {
  position: absolute;
  width: 100%;
  height: 100%;
  padding: 20px;
}
.note-detail textarea {
  border: none;
  resize: none;
  outline: none;
  font-size: 14px;
  font-family: 'Monaco', courier, monospace;
}
.note-detail code {
  color: #f66;
}
#trash {
  display: flex;
  align-items: stretch;
  background-color: #fff;
  flex: 1;
}
#trash .note-bar .action {
  float: right;
  margin-left: 10px;
  padding: 2px 4px;
  font-size: 12px;
}
